.header-color {
  background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid {
  background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

text-align: center;
/* position: fixed; */
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.awatar-img {
height: 250px;
padding-top: 4em;
}
.projects-grid {
  display: flex;
}

.contact-body{
  margin: auto;
  /* background: grey; */
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  /* border: 2px solid red; */
  margin-bottom: 20px;
  
}

/*   Resume Page Css   */

.resume-right-col {
  background: #27221F;
  color: white;
  padding: 2em;
}

.contact-grid h2 {
  font-family: 'Anton',sans-serif;

}
.contact-grid p{
font-family: 'Oxygen',sans-serif;
font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 20%;
  
  padding-right: 15rem;
}

.contact-list i{
  font-size: 66px;
  padding-right: 2rem;

}
.contact-list {
  padding-right:2rem;
}
.heading-side {
  padding-right: 15rem;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}
.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
  text-align: center;
}
.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
   padding: 1em;
   text-align: center;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}
.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}